.title {
  padding: 10px;
  padding-left: 5px;
}

p {
  padding: 10px;
  padding-left: 5px;
}

li {
  list-style: none;
  margin-bottom: 10px;
  overflow: hidden;
  display: block;
  position: relative;
}
ul {
  padding-left: 0;
}

input[type="text"],
input[type="email"] {
  width: 100%;
  height: 50px;
  font-size: 16px;
  padding: 0 10px;
}

textarea {
  width: 100%;
  height: 50px;
  font-size: 16px;
  padding: 10px;
  min-height: 150px;
}

div.contact-page-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
}

.send-btn {
  font-size: .7rem;
  letter-spacing: 3px;
  text-decoration: none;
  padding: 8px 10px;
  border: 1px solid;
  float: left;
  border-radius: 4px;
  float: right;
  text-align: center;
  margin-right: 10px;
}

div .contact-page-container {
  margin-bottom: 3rem;
}