* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.content {
  max-width: 800px;
  margin: 30px auto;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

body {
  font-family: "Quicksand";
}
